.smf__ContactInfo {
	display: flex;
	justify-content: center;

	gap: clamp(2rem, 3vw, 5rem);
	align-items: center;
	padding: clamp(4rem, 3.5vw, 7rem);
}
.smf__ContactInfo-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.smf__ContactInfo-content img {
	width: 40px;
	height: 40px;
	margin-right: 20px;
}
.smf__ContactInfo-content h3 {
	margin-bottom: 2rem;
	font-size: 30px;
}
.smf__ContactInfo-content p,
.smf__ContactInfo-content a {
	font-size: clamp(17px, 2vw, 20px);
}
.smf__ContactInfo-content p {
	color: var(--color-grey);
}
.smf__ContactInfo-content a {
	color: #0070f3;
}
.smf__ContactInfo-content_location {
	display: flex;
	align-items: flex-start;
	margin-bottom: 2.5rem;
}
.smf__ContactInfo-content_phone,
.smf__ContactInfo-content_mail {
	display: flex;
	align-items: center;
	margin-bottom: 2.5rem;
}
@media screen and (max-width: 900px) {
	.smf__ContactInfo-content h3 {
		text-align: center;
	}
	.smf__ContactInfo-content_mail {
		margin-bottom: 0;
	}
	.smf__ContactInfo {
		flex-wrap: wrap;
		gap: 2rem;
		padding: 1rem;
	}
}
