@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700;800&family=Open+Sans:wght@800&family=Ubuntu:wght@700&display=swap');

:root {
	--font-family-1: 'Manrope', sans-serif;
	--font-family-2: 'Open Sans', sans-serif;
	--font-family-3: 'Ubuntu', sans-serif;

	--color-gradient: linear-gradient(
			to bottom,
			#323232 0%,
			#3f3f3f 40%,
			#1c1c1c 150%
		),
		linear-gradient(to top, #dbdbdb66 0%, #00000040 200%);
	--boxShadow: #33333338 8px 12px 25px, #33333338 0px 12px 10px;
	--color-grey: #6b6b6b;
	--color-red: #b80000c2;
	--color-white: #ffffff;
	--color-getintouch: #5873ff;
	--color-text: #c6cfff;
	--color-subtext: #ff957342;
	scrollbar-color: #b80000 #00000040 !important;
}
.smf-button {
	display: inline-block;
	cursor: pointer;
	border: none;
	padding: 0.7rem 2.5rem;
	border-radius: 7px;
	background-color: #ffffff;
	color: #494949;
	font-weight: 700;
	font-size: clamp(19px, 3vw, 25px);
	box-shadow: 0 4px 14px 0 #5a5a5a83;
}
.slide-in-left {
	-webkit-animation: slide-in-left 0.75s ease-out both;
	animation: slide-in-left 0.75s ease-out both;
}
@-webkit-keyframes slide-in-left {
	0% {
		-webkit-transform: translateX(-50px);
		transform: translateX(-50px);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-left {
	0% {
		-webkit-transform: translateX(-50px);
		transform: translateX(-50px);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}
.slide-out-right {
	-webkit-animation: slide-out-right 0.75s ease-out 0.2s both;
	animation: slide-out-right 0.75s ease-out 0.2s both;
}
@-webkit-keyframes slide-out-right {
	0% {
		-webkit-transform: translateX(0);
		transform: translateY(0);
		opacity: 0.9;
	}
	100% {
		-webkit-transform: translateX(-50px);
		transform: translateX(-50px);
		opacity: 0;
	}
}
@keyframes slide-out-right {
	0% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 0.9;
	}
	100% {
		-webkit-transform: translateX(-50px);
		transform: translateX(-50px);
		opacity: 0;
	}
}
.fade-in {
	-webkit-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
	animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.fade-in-fast {
	-webkit-animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
	animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.fade-out-fast {
	-webkit-animation: fade-out 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
	animation: fade-out 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@-webkit-keyframes fade-out {
	0% {
		opacity: 0.9;
	}
	100% {
		opacity: 0;
	}
}
@keyframes fade-out {
	0% {
		opacity: 0.9;
	}
	100% {
		opacity: 0;
	}
}
.scale-up-center:hover {
	-webkit-animation: scale-up-center 0.25s cubic-bezier(0.39, 0.575, 0.565, 1)
		both;
	animation: scale-up-center 0.25s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@-webkit-keyframes scale-up-center {
	0% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
	100% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}
}
@keyframes scale-up-center {
	0% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
	100% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}
}
