.smf__WhatWeDo-container {
	display: flex;
	flex-direction: column;
	padding: 2rem;
	justify-content: center;
	align-items: center;
}
.smf__WhatWeDo-container_features {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}
.smf__WhatWeDo-container_title {
	display: flex;
	flex-direction: column;
	align-items: center;
	opacity: 0;
	text-align: center;
}
.smf__WhatWeDo-container_title p {
	font-size: 12px;
	color: var(--color-grey);
	font-weight: 700;
}
.smf__WhatWeDo-container_title h1 {
	font-size: clamp(34px, 4vw, 44px);
	margin: 1rem 0 2rem 0;
}
